.SearchBar {
    display: flex;
    align-items: baseline;
    justify-content: center;
    padding-top: 7rem;
    margin-bottom: 7rem;
  }
  
  .SearchBar input {
    width: 287px;
    padding: 0.88rem 0;
    border: 1px solid #fff;
    color: #010c3f;
    text-align: left;
    font-size: 1rem;
    text-indent: 10px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  
  .SearchBar input:focus {
    outline: none;
  }
  
  .SearchButton {
    cursor: pointer;
    width: 8.11rem;
    height: 54.6px;
    padding: 0px 0px;
    background-color: #6c41ec;
    text-align: center;
    font-size: .833rem;
    transition: background-color .25s;
    border: 0px;
    color: #fff;
    font-weight: 500;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
  }
  
  .SearchButton:hover {
    background-color: rgba(108, 65, 233, .7);
  }

  @media only screen and (max-width: 1020px) {
    .SearchBar {
      padding-top: 5rem;
    margin-bottom: 4rem;
    }
  }