.Track {
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(256, 256, 256, 0.8);
}

.Track-action {
  cursor: pointer;
  padding: .5rem;
  font-size: 1.05rem;
  transition: color .25s;
  border: 0px;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
}

.Track-action:hover {
  color: rgba(265, 265, 265, .5);
}

.Track-preview {
  cursor: pointer;
  font-size: 1.05rem;
  transition: color .25s;
  border: 0px;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.Track-preview:hover {
  color: rgba(265, 265, 265, .5);
}


.Track-information {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px 0px 15px 0px;
  max-width: calc(100% - 69px);
}

.Track-information h3 {
  margin-bottom: .22rem;
}

.Track-information p {
  font-size: .83rem;
  font-weight: 300;
  color: rgba(256, 256, 256, 0.8);
}

/* Sets the containers height and width */
.base-timer {
  position: relative;
  height: 40px;
  width: 40px;
}

.base-timer__svg {
  height: 40px;
  width: 40px;
}

svg path {
  height: 40px;
  width: 40px;
}

/* Removes SVG styling that would hide the time label */
.base-timer__circle {
  height: 31px;
  width: 31px;
  fill: none;
  stroke: none;
}

/* The SVG path that displays the timer's progress */
.base-timer__path-elapsed {
  stroke-width: 5px;
  stroke: white;
  stroke-linecap: round;
  stroke-dasharray: 88;
  stroke-dashoffset: 88;
  animation: dash 30s linear;
}

@keyframes dash {
  0% {
    stroke-dashoffset: -88;
  }
  
  100% {
    stroke-dashoffset: 0;
  }
}

.base-timer__label {
  position: absolute;
  
  /* Size should match the parent container */
  width: 40px;
  height: 40px;
  
  /* Keep the label aligned to the top */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  
  /* Create a flexible box that centers content vertically and horizontally */
  display: flex;
  align-items: center;
  justify-content: center;

  /* Sort of an arbitrary number; adjust to your liking */
  font-size: 24px;
}

@keyframes circletimer {
  0% {
    stroke-dashoffset: 100.5;
    stroke-dasharray: 100.5;
  }
  100% {
    stroke-dashoffset: 0;
    stroke-dasharray: 100.5;
  }  
}

svg {
  background-color: transparent;
  position: absolute;
  background-color: transparent;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateZ(-90deg);
}

.circle {
  stroke: white;
  stroke-width: 3;
  fill: transparent;
  stroke-dashoffset: 100.5;
  stroke-dasharray: 100.5;
}

.circle-anim {
  stroke: white;
  stroke-width: 3;
  fill: transparent;
  stroke-dashoffset: 100.5;
  stroke-dasharray: 100.5;
  animation: 30s circletimer linear;
}

.fa-solid, .fas {
  font-size: 11px;
}